import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    emp: {
        Name: "Welcome",
        Age: 53,
        Email: "ante.blandit@disparturient.ca",
        "Date of joining": "10/06/2019",
        "Phone number": "1628101902299",
        Street: "493 Iaculis Rd.",
        City: "Lewiston",
        Zip: "42591-180",
        Region: "Maine",
        Country: "Sudan",
        Info: "est. Nunc ullamcorper, velit in aliquet lobortis, nisi nibh lacinia",
        EmployeeID: 101,
    }
};

export const employeeSlice = createSlice({
    name: 'employee',
    initialState,
    reducers: {
        viewslice: (state, action) => {
            // console.log(action.payload);
            // console.log(state);
            state.emp = action.payload;
        }
    }
})

export const { viewslice } = employeeSlice.actions;
export default employeeSlice.reducer;
