import React from "react";
import "../css/calculator.css";

const Calculator = () => {
  const [output, setOutput] = React.useState("");

  const display = (input) => {
    // output += input;
    setOutput(output + input);
    console.log(output);
  };

  const acFunction = () => {
    setOutput("");
  };

  const calculate = () => {
    let result = eval(output);
    setOutput(result);
  };
  const clearFunction = () => {
    let outputArray = output.split("");
    if (outputArray.length > 0) {
      outputArray.pop();
    }
    setOutput(outputArray.join(""));
  };

  return (
    <div className="calculator">
      <ul>
        <input type="text" placeholder="0" value={output} />
      </ul>
      <ul>
        <li>
          <button onClick={() => display("7")}>
            <b>7</b>
          </button>
        </li>
        <li>
          <button onClick={() => display("8")}>
            <b>8</b>
          </button>
        </li>
        <li>
          <button onClick={() => display("9")}>
            <b>9</b>
          </button>
        </li>
        <li>
          <button onClick={() => display("/")}>
            <b>/</b>
          </button>
        </li>
      </ul>
      <ul>
        <li>
          <button onClick={() => display("4")}>
            <b>4</b>
          </button>
        </li>
        <li>
          <button onClick={() => display("5")}>
            <b>5</b>
          </button>
        </li>
        <li>
          <button onClick={() => display("6")}>
            <b>6</b>
          </button>
        </li>
        <li>
          <button onClick={() => display("*")}>
            <b>*</b>
          </button>
        </li>
      </ul>

      <ul>
        <li>
          <button onClick={() => display("1")}>
            <b>1</b>
          </button>
        </li>
        <li>
          <button onClick={() => display("2")}>
            <b>2</b>
          </button>
        </li>
        <li>
          <button onClick={() => display("3")}>
            <b>3</b>
          </button>
        </li>
        <li>
          <button onClick={() => display("-")}>
            <b>-</b>
          </button>
        </li>
      </ul>
      <ul>
        <li>
          <button onClick={() => display("0")}>
            <b>0</b>
          </button>
        </li>
        <li>
          <button onClick={() => display(".")}>
            <b>.</b>
          </button>
        </li>
        <li>
          <button onClick={() => calculate()}>
            <b>=</b>
          </button>
        </li>
        <li>
          <button onClick={() => display("+")}>
            <b>+</b>
          </button>
        </li>
      </ul>
      <ul>
        <button className="action" onClick={() => acFunction()}>
          <b>AC</b>
        </button>
        <button className="action" onClick={() => clearFunction()}>
          <b>Clear</b>
        </button>
      </ul>
    </div>
  );
};

export default Calculator;
