import React from "react";
import "../css/signup.css";
import { useForm } from "react-hook-form";

const Signup = () => {
  const {
    handleSubmit,
    register,
    formState: { errors },
  } = useForm();

  const onSubmit = (data) => {
    console.log(data);
  };
  return (
    <form className="signupform" onSubmit={handleSubmit(onSubmit)}>
      <div className="form-group">
        <label htmlFor="">
          <i className="fa-solid fa-user"></i> Username
        </label>
        <input
          type="text"
          name="username"
          {...register("user", {
            required: {
              value: true,
              message: "Username is required",
            },
            minLength: {
              value: 3,
              message: "Username should have atleast 3 characters",
            },
          })}
        />
        <small>{errors.user?.message}</small>
      </div>
      <div className="form-group">
        <label htmlFor="">
          <i className="fa-solid fa-envelope"></i> Email
        </label>
        <input type="text" name="email" {...register("email")} />
      </div>
      <div className="form-group">
        <label htmlFor="">
          <i className="fa-solid fa-key"></i> Password
        </label>
        <input
          type="password"
          name="password"
          {...register("pass", {
            required: {
              value: true,
              message: "Password is required",
            },
            minLength: {
              value: 8,
              message: "Password should have atleast 8 characters",
            },
            pattern: {
              value: /^(?=.*[a-z])(?=.*[0-9])[a-z0-9]{8,}$/,
              message: "Password should have one small letter and number",
            },
          })}
        />
        <small>{errors.pass?.message}</small>
      </div>
      <div className="form-group">
        <label htmlFor="">
          <i className="fa-solid fa-key"></i> Re-type Password
        </label>
        <input
          type="password"
          name="confirmPassword"
          {...register("cnfrmPass", {
            required: {
              value: true,
              message: "Password is required",
            },
            minLength: {
              value: 8,
              message: "Password should have atleast 8 characters",
            },
          })}
        />
        <small>{errors.cnfrmPass?.message}</small>
      </div>
      <div className="form-group" style={{ textAlign: "center" }}>
        <button>Sign up</button>
      </div>
    </form>
  );
};

export default Signup;
