import React from "react";

class Welcome extends React.Component {
  // const name = 'Siva Ranjini K'
  name = "Siva Ranjini K";
  //   const bye = () => {
  //     alert('Bye');
  //   }
  bye = () => {
    alert("Bye" + " " + this.name);
  };
  render() {
    return (
      <div>
        <p>Welcome {this.name}</p>
        <button onClick={() => this.bye()}>Bye</button>
      </div>
    );
  }
}

export default Welcome;
